import $ from 'jquery';

$(() => {
	$('.faq-item__title .js-toggle').on('click', function(){
		var $toggle = $(this);
		var $item = $(this).parents('.faq-item:first');
		var $collapse = $(this).next('.collapse')

		if ($item.hasClass('open')) {
			$item.removeClass('open');
			$collapse.collapse('hide');
		} else {
			$item.addClass('open');
			$collapse.collapse('show');
		}

		return false;
	});
});



